import request from "./request.js";

const qiniuUploader = require("./qiniuUploader");
export default class fileUpload extends request {
	constructor(props) {
		// 
		super(props);
	}
	//
	qnImgUpload(data = {}, options = {}) {
		const _this = this;
		return new Promise((resolve, reject) => {
			uni.chooseImage({
				count: data.count || 9, //9
				sizeType: data.sizeType || ['original', 'compressed'], //
				sourceType: data.sourceType || ['album', 'camera'], //
				success: function(res) {
					_this.qnFileUpload({
						files: res.tempFiles,
						...data
					}, options).then(resolve, reject);
				}
			});
		});
	}
	//
	randomChar(l, url = "") {
		const x = "0123456789qwertyuioplkjhgfdsazxcvbnm";
		let tmp = "";
		let time = new Date();
		for (let i = 0; i < l; i++) {
			tmp += x.charAt(Math.ceil(Math.random() * 100000000) % x.length);
		}
		return (
			"file/" +
			url +
			time.getTime() +
			tmp
		);
	}
	//
	qnFileUpload(data = {}, options = {}) {
		const _this = this;
		let requestInfo = {
			...data,
			...this.config,
			...options,
			header: {},
			method: "FILE"
		};
		return new Promise((resolve, reject) => {
			//
			if (_this.requestStart) {
				let requestStart = _this.requestStart(requestInfo);
				if (typeof requestStart == "object") {
					requestInfo.load = requestStart.load;
					requestInfo.files = requestStart.files;
				} else {
					//
					_this.requestEnd && _this.requestEnd(requestInfo, {
						errMsg: "",
						statusCode: 0
					});
					reject({
						errMsg: "",
						statusCode: 0
					});
					return;
				}
			}
			if (Array.isArray(requestInfo.files)) {
				let len = requestInfo.files.length;
				let imageList = new Array;
				//
				_this.get("api/kemean/aid/qn_upload").then(data => {
					/*
					 *
					 *visitPrefix:
					 *token:token
					 *folderPath:
					 */
					uploadFile(0);

					function uploadFile(i) {
						let fileData = {
							fileIndex: i,
							files: requestInfo.files,
							size: requestInfo.files[i].size
						};




						// 
						qiniuUploader.upload(requestInfo.files[i].path, (res) => {
							fileData.url = res.imageURL;
							requestInfo.onEachUpdate && requestInfo.onEachUpdate({
								url: res.imageURL,
								...fileData
							});
							imageList.push(res.imageURL);
							if (len - 1 > i) {
								uploadFile(i + 1);
							} else {
								//
								_this.requestEnd && _this.requestEnd(requestInfo, {
									errMsg: "request:ok",
									statusCode: 200,
									data: imageList
								});
								resolve(imageList);
							}
						}, (error) => {
							console.log('error: ' + error);
							//
							_this.requestEnd && _this.requestEnd(requestInfo, error);
							reject(error)
						}, {
							region: 'SCN', //
							domain: data.visitPrefix, // bucket 
							key: _this.randomChar(8, data.folderPath),
							uptoken: data.token, //  uptoken
							uptokenURL: 'UpTokenURL.com/uptoken' // 
						}, (res) => {
							console.log(requestInfo);
							requestInfo.onProgressUpdate && requestInfo.onProgressUpdate(Object.assign({}, fileData, res));
							// console.log('', res.progress)
							// console.log('', res.totalBytesSent)
							// console.log('', res.totalBytesExpectedToSend)
						});
					}
				});
			} else {
				//
				_this.requestEnd && _this.requestEnd(requestInfo, {
					errMsg: "files ",
					statusCode: 0
				});
				reject({
					errMsg: "files ",
					statusCode: 0
				});
			};
		});

	}
	//
	urlImgUpload(url = '', data = {}, options = {}) {
		const _this = this;

		return new Promise((resolve, reject) => {
			uni.chooseImage({
				count: data.count || 9, //9
				sizeType: data.sizeType || ['original', 'compressed'], //
				sourceType: data.sourceType || ['album', 'camera'], //
				success: function(res) {
					_this.urlFileUpload(url, {
						...data,
						files: res.tempFiles
					}, options).then(resolve, reject);
				}
			});
		});
	}
	//
	urlFileUpload(url = '', data = {}, options = {}) {
		let requestInfo = this.getDefault({
			...data,
			method: "FILE",
			url:url
		}, options);
		const _this = this;
		
		return new Promise((resolve, reject) => {
			//
			if (_this.requestStart) {
				let requestStart = _this.requestStart(requestInfo);
				if (typeof requestStart == "object") {
					requestInfo.data = requestStart.data;
					requestInfo.header = requestStart.header;
					requestInfo.isPrompt = requestStart.isPrompt;
					requestInfo.load = requestStart.load;
					requestInfo.isFactory = requestStart.isFactory;
					requestInfo.files = requestStart.files;
				} else {
					//
					_this.requestEnd && _this.requestEnd(requestInfo, {
						errMsg: "",
						statusCode: 0
					});
					reject({
						errMsg: "",
						statusCode: 0
					});
					return;
				}
			}
			if (Array.isArray(requestInfo.files)) {







































































				const len = requestInfo.files.length - 1;
				let fileList = new Array;
				fileUpload(0);

				function fileUpload(i) {
					let fileData = {
						fileIndex: i,
						files: requestInfo.files,
						size: requestInfo.files[i].size
					};




					let config = {
						url: requestInfo.httpUrl,
						filePath: requestInfo.files[i].path,
						header: requestInfo.header, //
						name: requestInfo.name || "file",
						success: (response) => {
							if (typeof(response.data) == "string") {
								response.data = JSON.parse(response.data);
							}
							//
							if (requestInfo.isFactory && _this.dataFactory) {
								//
								_this.dataFactory({
									...requestInfo,
									response: response,
									resolve: function(data) {
										requestInfo.onEachUpdate && requestInfo.onEachUpdate({
											data: data,
											...fileData
										});
										fileList.push(data);
										if (len <= i) {
											//
											_this.requestEnd && _this.requestEnd(requestInfo, {
												errMsg: "request:ok",
												statusCode: 200,
												data: fileList
											});
											resolve(fileList);
										} else {
											fileUpload(i + 1);
										}
									},
									reject: function(err) {
										//
										_this.requestEnd && _this.requestEnd(requestInfo, {
											errMsg: "",
											statusCode: 0,
											data: err
										});
										reject(err);
									}
								});
							} else {
								requestInfo.onEachUpdate && requestInfo.onEachUpdate({
									data: response,
									...fileData
								});
								fileList.push(response);
								if (len <= i) {
									//
									_this.requestEnd && _this.requestEnd(requestInfo, {
										errMsg: "request:ok",
										statusCode: 200,
										data: fileList
									});
									resolve(fileList);
								} else {
									fileUpload(i + 1);
								}
							}
						},
						fail: (err) => {
							//
							_this.requestEnd && _this.requestEnd(requestInfo, err);
							reject(err);
						}
					};
					if (requestInfo.data) {
						config.formData = requestInfo.data;
					}
					const uploadTask = uni.uploadFile(config);
					uploadTask.onProgressUpdate(res => {
						requestInfo.onProgressUpdate && requestInfo.onProgressUpdate(Object.assign({}, fileData, res));
					});
				}

			} else {
				//
				_this.requestEnd && _this.requestEnd(requestInfo, {
					errMsg: "files ",
					statusCode: 0
				});
				reject({
					errMsg: "files ",
					statusCode: 0
				})
			}
		});
	}
}
