
import $store from "@/store";
import toLogin from "@/libs/login";
import { VUE_APP_API_URL } from "@/utils/index";
import request from "./request/index";

//newrequest
let $http = new request({
	//
	baseUrl: VUE_APP_API_URL,
	//
	fileUrl: VUE_APP_API_URL,
	//content-type
	//
	//--
	isbackall: false,
	isPrompt: true,
	//
	load: true,
	//
	islogin:false,
	//
	isFactory: true,
	back:false
});
//
let requestNum = 0;
//
$http.requestStart = function(options) {

	if (options.load) {
		if (requestNum <= 0) {
			//
			uni.showLoading({
				title: options.title?options.title:'',
				mask: true
			});
		}
		requestNum += 1;
	}
	// 
	if (options.method == "FILE" && options.maxSize) {
		// : options.maxSize 
		maxSize = options.maxSize;
		for (let item of options.files) {
			if (item.size > maxSize) {
				setTimeout(() => {
					uni.showToast({
						title: "",
						icon: "none"
					});
				}, 500);
				return false;
			}
		}
	}
	//token

	if(options.islogin){
		
		if($store.state.app.hasLogin){
			
			let openid = $store.state.app.openid;
			
			if(openid.indexOf("sns_wa_") == -1 &&openid.indexOf("wap_user_") == -1){
				openid = 'sns_wa_'+openid;
			}
			options.data['openid'] = openid;
		}else{
			
			uni.showToast({
				title : "",
				icon :"none",
				success: function() {
					if(options.back){
						toLogin(false);
					}else{
						toLogin(true);
					}
					
					
				}
			});
			return false;
		}
	}
	return options;
}
//
$http.requestEnd = function(options, resolve) {
	//
	
	if (options.load) {
		requestNum = requestNum - 1;
		if (requestNum <= 0) {
			uni.hideLoading();
		}
	}
	if (resolve.errMsg && resolve.statusCode && resolve.statusCode > 300) {
		setTimeout(() => {
			uni.showToast({
				title: "",
				icon: "none"
			});
		}, 500);
	}
}
//
let loginPopupNum = 0;
//
//
$http.dataFactory = function(res) {
	// console.log("", {
	// 	httpUrl: res.httpUrl,
	// 	resolve: res.response,
	// 	header: res.header,
	// 	data: res.data,
	// 	method: res.method,
	// });
	// 
	
	if (res.response.statusCode && res.response.statusCode == 200) {
		let httpData = res.response.data;
		/*********()*********/
		//
		
		if (httpData.error == 0) {
			// (then)
			if(res.isbackall){
				res.resolve(res.response);
			}
			Object.getOwnPropertyNames(httpData).length >=3 ?res.resolve(httpData) : res.resolve(httpData.data); 
		} else { //
	
			if (res.isPrompt) { //
				setTimeout(function() {
					uni.showToast({
						title: httpData.message, //
						icon: "none",
						duration: 3000
					});
				}, 500);
			}
			// (catch)
			res.reject(res.response);
		}

		/*********()*********/

	} else {
		// (catch)
		res.reject(res.response);
	}
};
export default $http;
